	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"We’ve come a long way in the last 20 years. We start our journey in the late 80s and our \"discovery\" of design principles such as The Open Closed Principle and the Liskov Substitution Principle. In the middle 90s, we discovered that these principle led to repeating patterns of design. We gave those patterns names such as Visitor and Decorator. At the turn of the millennium we found that the benefits gained from the principles and patterns could be amplified by combining them with practices such as Test Driven Development and Continuous Integration. And now, as the decade is coming to a close, we have found that these principles, patterns, and practices have driven us to define a true profession. What will that profession require of us, and who among us can truly claim to be professional?","meta":{"duration":"PT1H8S"},"presenter":{"name":"\"Uncle Bob\" - Robert Cecil Martin"},"source":{"id":"zwtg7lIMUaQ","provider":"youtube"},"slug":"professional-software-development","tag":["Software Engineering","Carrer","Agile","NDC Conference 2012"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/zwtg7lIMUaQ/default.jpg","height":90,"width":120}},"title":"Professional Software Development"}}]}},"pathContext":{"slug":"software-engineering","tag":"Software Engineering"}}

